import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Row, Col, Card, Form, Modal, Button } from 'react-bootstrap';

import { useCollectionData } from 'react-firebase-hooks/firestore';
import { useState } from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { /*faEdit,*/ faTrashAlt, faPlus } from '@fortawesome/free-solid-svg-icons';

import './Avvisi.css';

import firebase from 'firebase/app';
import dayjs from 'dayjs';

const Avvisi = (props) => {

    const firestore = props.firestore;

    const avvisiRef = firestore.collection('aggregati');
    const query = avvisiRef.limit(1);
    let [avvisi] = useCollectionData(query, {idField: 'id'});

    if (avvisi) {
        avvisi = avvisi[0].list;

        let idx = 0;
        avvisi = avvisi.map(a => {
            return {id: idx++, date: a.date, title: a.title, text: a.text};
        });

        avvisi.sort((a, b) => (a.date < b.date) ? 1 : -1);
    }

    //avvisi = undefined;

    const [showAdd, setShowAdd] = useState(false);
    const handleCloseAdd = () => setShowAdd(false);
    const handleShowAdd = () => setShowAdd(true);

    return (
        <>
        <Container>
            <br />
            <h1 className="gradient-text">Avvisi {props.user && <FontAwesomeIcon icon={faPlus} className='icon-add' onClick={handleShowAdd}/>}</h1>
            {
                avvisi && avvisi.map(a => <ElemAvviso 
                    key={a.id}
                    avviso={a}
                    user={props.user}
                    avvisiRef={avvisiRef}
                />)
            }
            <br />
        </Container>

        {props.user && <ModalAdd avvisi={avvisi} firestore={firestore} auth={props.auth} avvisiRef={avvisiRef} show={showAdd} handleClose={handleCloseAdd} />}
        </>
    );
}

const ElemAvviso = (props) => {

    const { text, title, date } = props.avviso;

    const deleteAvviso = async () => {
        props.avvisiRef.doc('avvisi').update({
            list: firebase.firestore.FieldValue.arrayRemove({date: date, title: title, text: text})
        });
    };

    return (
        <>
        <br />
        <Row>
            <Col md={12}>
            <Card className="card-custom">
                <Card.Body>
                    <Card.Title className="c-title">
                        <Row>
                            <Col sm={props.user ? 10 : 12}>{title}</Col>
                            { props.user &&
                            <>
                                {/*<Col sm={1}>
                                    <FontAwesomeIcon icon={faEdit} className='icon-edit' />
                                </Col>*/}
                                <Col sm={1}>
                                    <FontAwesomeIcon icon={faTrashAlt} onClick={deleteAvviso} className='icon-trash' />
                                </Col>
                            </>
                            }
                        </Row>
                    </Card.Title>
                    <Card.Subtitle className="mb-2 text-muted c-subtitle">{date && dayjs(date.seconds * 1000).format('DD/MM/YYYY')}</Card.Subtitle>
                    <Card.Text className="c-text">{text}</Card.Text>
                </Card.Body>
            </Card>
            </Col>
        </Row>
        </>
    );
}

const ModalAdd = (props) => {

    const [titleVal, setTitleVal] = useState('');
    const [textVal, setTextVal] = useState('');

    const submitText = async (e) => {
        e.preventDefault();

        await props.avvisiRef.doc('avvisi').update({
            list: firebase.firestore.FieldValue.arrayUnion({date: new firebase.firestore.Timestamp(dayjs().unix(), 0), title: titleVal, text:textVal})
        });

        handleClose();
    };

    const handleClose = () => {
        setTextVal('');
        setTitleVal('');
        props.handleClose();
    }

    return (
        <Modal size="lg" show={props.show} onHide={handleClose} >
            <Modal.Header className="modal-full">
                <Modal.Title className="modal-title gradient-text ">Aggiungi un avviso</Modal.Title>
            </Modal.Header>

            <Form onSubmit={submitText}>
            <Modal.Body className="modal-full">
                <Form.Group as={Row} controlId="formPlaintextEmail">
                    <Form.Label column sm="2" className="modal-text">
                        Titolo
                    </Form.Label>
                    <Col sm="10">
                        <Form.Control className="round-input-box" type="text" value={titleVal} onChange={e => setTitleVal(e.target.value)} required/>
                    </Col>
                </Form.Group>

                <Form.Group as={Row} controlId="formPlaintextPassword">
                    <Form.Label column sm="2" className="modal-text">
                        Testo
                    </Form.Label>
                    <Col sm="10">
                        <Form.Control as="textarea" rows={6} className="round-input-box" value={textVal} onChange={e => setTextVal(e.target.value)} required/>
                    </Col>
                    </Form.Group>
            </Modal.Body>

            <Modal.Footer className="modal-foot">
                <Button onClick={handleClose} className="modal-text close-btn">
                    Chiudi
                </Button>
                <Button type="submit" className="modal-text submit-btn">
                    Aggiungi
                </Button>
            </Modal.Footer>
            </Form>
        </Modal>
    );
};

export default Avvisi;